* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #eff3f7;
}
html,
body,
#root,
.container {
  height: 100%;
  display: flex;
  justify-content: center;
}
.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.movies {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  width: 80%;
  padding-top: 70px;
  padding: 50px;
  justify-content: space-between;
}
.movies .movie {
  display: flex;
  width: 45%;
  background-color: white;
  margin-bottom: 70px;
  align-items: flex-start;
}
.movie img {
  position: relative;
  top: -50px;
  max-width: 120px;
  margin-right: 30px;
}
.movie h3,
.movie h5 {
  margin: 0;
  font-weight: 300;
}
.movie h3 {
  margin-bottom: 5px;
  font-size: 24px;
}
.movie h5,
.movie,
.year {
  opacity: 0.8;
}
